body {
  font-family: "Open Sans", sans-serif;
  color: #333333;
  font-size: 16px;
}

#content {
  margin: 0 16px;
  max-width: 480px;
}

#name {
  font-weight: 700;
  color: #ff851b;
}

#tagline {
  font-size: 1.2em;
  line-height: 1.4em;
}

a,
a:visited {
  color: #0074d9;
  text-decoration: none;
}

a:hover {
  color: #001f3f;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  margin-bottom: 8px;
}

